// projectionMatrix  --  .projectionMatrix

// viewMatrix --.matrixWorldInverse

// modelMatrix -- .matrixWorld


export default {
  vertex: `  
 void main() {
    gl_Position = projectionMatrix * viewMatrix * modelMatrix * vec4(position, 1.0);
  }
`,
  fragment: `
 void main() {
  gl_FragColor = vec4(1.0,0.5,0.5,1.0);
}
`
}